import * as React from 'react';
import Chevron from 'nav-frontend-chevron';
import * as moment from 'moment';
import { DatovelgerAvgrensninger } from './types';
import { erDatoEtterMinDato, erDatoFÃ¸rSluttdato } from './utils/datovalidering';

// interface NavbarKnapp {
// 	label: string;
// 	onClick: (event: React.MouseEvent<HTMLButtonElement>) => void;
// 	disabled?: boolean;
// }

interface Props {
	mÃ¥ned: Date;
	byttMÃ¥ned: (month: Date) => void;
	avgrensninger?: DatovelgerAvgrensninger;
}

const Navbar: React.StatelessComponent<Props> = ({
	mÃ¥ned,
	byttMÃ¥ned,
	avgrensninger
}) => {
	const forrige = moment(mÃ¥ned)
		.add(-1, 'months')
		.endOf('month');

	const neste = moment(mÃ¥ned)
		.add(1, 'months')
		.startOf('month');

	const onClick = (evt: React.MouseEvent<HTMLButtonElement>, mnd: Date) => {
		evt.preventDefault();
		evt.stopPropagation();
		byttMÃ¥ned(mnd);
	};

	return (
		<div className="nav-dagvelger__navbar">
			<button
				className="nav-dagvelger__navbar__knapp nav-dagvelger__navbar__knapp--forrige"
				type="button"
				onClick={(e) => onClick(e, forrige.toDate())}
				disabled={
					avgrensninger
						? !erDatoEtterMinDato(forrige.toDate(), avgrensninger.minDato)
						: false
				}
				aria-label={`GÃ¥ til ${forrige.format('DDDD YYYY')}`}
			>
				<Chevron type="venstre" />
			</button>
			<button
				type="button"
				onClick={(e) => onClick(e, neste.toDate())}
				disabled={
					avgrensninger
						? !erDatoFÃ¸rSluttdato(neste.toDate(), avgrensninger.minDato)
						: false
				}
				aria-label={`GÃ¥ til ${neste.format('DDDD YYYY')}`}
				className="nav-dagvelger__navbar__knapp nav-dagvelger__navbar__knapp--neste"
			>
				<Chevron type="hÃ¸yre" />
			</button>
		</div>
	);
};

export default Navbar;



